import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import axios from 'axios';
import { img_500, unavailable, unavailableLandscape } from '../../config/config';
import { Button } from '@material-ui/core';
import YoutubeIcon from '@material-ui/icons/YouTube'
import './ContentModal.css'
import Carousel from '../Carousel/Carousel'

const useStyles = makeStyles((theme) => ({
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    paper: {
        width: '90%',
        height: '80%',
        backgroundColor: '#39445a',
        border: '1px solid #282c34',
        borderRadius: 10,
        color: 'white',
        boxShadow: theme.shadows[5],
        padding: theme.spacing(1, 1, 3)
    },
}));

function ContentModal({ children, media_type, id }) {

    const classes = useStyles();
    const [open, setOpen] = useState(false);
    const [content, setContent] = useState([]);
    const [video, setVideo] = useState();

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const fetchData = async () => {
        const { data } = await axios.get(
            `https://api.themoviedb.org/3/${media_type}/${id}?api_key=033f874c1d083b7205fde0c0bb83e3ef&language=en-US`
        )

        setContent(data)
    }

    const fetchVideo = async () => {
        const { data } = await axios.get(
            `https://api.themoviedb.org/3/${media_type}/${id}/videos?api_key=033f874c1d083b7205fde0c0bb83e3ef&language=en-US`
        )

        setVideo(data.results[0]?.key)
    }

    useEffect(() => {
        fetchData()
        fetchVideo()
    }, [id, media_type])

    return (
        <>
            <div className='media' type="button" onClick={handleOpen}>
                {children}
            </div>

            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    {content && (
                        <div className={classes.paper}>
                            <div className='contentModal'>
                                <img
                                    className='contentModal_portrait'
                                    src={content.poster_path ? `${img_500}/${content.poster_path}` : unavailable}
                                    alt={content.title || content.name}
                                />
                                <img
                                    className='contentModal_landscape'
                                    src={content.backdrop_path ? `${img_500}/${content.backdrop_path}` : unavailableLandscape}
                                    alt={content.title || content.name}
                                />
                                <div className='contentModal_about'>
                                    <span className='contentModal_title'>
                                        {content.name || content.title} (
                                        {(
                                            content.first_air_date || content.release_date || "-----"
                                        ).substring(0, 4)}
                                        )
                                    </span>
                                    {content.tagline && (
                                        <i className='tagline'>{content.tagline}</i>
                                    )}
                                    <span className='contentModal_description'>{content.overview}</span>
                                    {content.vote_average && (
                                        <span className='contentModal_list'><b>Rating: </b>{content.vote_average.toFixed(1)}</span>
                                    )}
                                    {content.release_date && (
                                        <span className='contentModal_list'><b>Release Date: </b>{content.release_date}</span>
                                    )}
                                    {content.genres && content.genres.length > 0 && (
                                        <span className='contentModal_list'><b>Genres: </b>{content.genres.map((c) => c.name).join(', ')}</span>
                                    )}
                                    {content.created_by && content.created_by.length > 0 && (
                                        <span className='contentModal_list'><b>Created By: </b>{content.created_by.map((c) => c.name).join(', ')}</span>
                                    )}
                                    {content.production_companies && content.production_companies.length > 0 && (
                                        <span className='contentModal_list'><b>Production Companies: </b>{content.production_companies.map((c) => c.name).join(', ')}</span>
                                    )}
                                    <div>
                                        <Carousel media_type={media_type} id={id} />
                                    </div>
                                    <Button
                                        variant='contained'
                                        startIcon={<YoutubeIcon />}
                                        color='secondary'
                                        target='_blank'
                                        href={`https://www.youtube.com/watch?v=${video}`}
                                    >
                                        Watch the Trailer
                                    </Button>
                                </div>
                            </div>
                        </div>
                    )}
                </Fade>
            </Modal>
        </>
    );
}

export default ContentModal;